      z-index: 2;
      max-width: 500px;

      @media screen and ($mobile_all) {
        margin-top: 25px;
      }

      @media screen and ($min1024) {
        @include vw(padding-top, 61px);
        @include vw(margin-left, 290px);
        @include vw(max-width, 650px);
      }

      @media screen and ($min1920) {
        padding-top: 61px;
