  .txt-blocks-content {
    @media screen and ($min1024) {
      max-width:40vw;
    }
  }

  .txt-blocks-content-2 {
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start;
    padding-top: 50px;
    max-width: 500px;

    @media screen and ($min1024) {
      @include vw(padding-top, 50px);
      @include vw(margin, 0 -30px);
      @include vw(max-width, 1053px);
    }

    @media screen and ($min1920) {
      padding-top: 50px;
      margin: 0 -30px;
      max-width: 1053px;
    }

    .txt-block {
      width: 100%;

      @media screen and ($mobile_all) {
        &:not(:last-of-type) {
          margin: 0 0 25px;
        }
      }

      @media screen and ($min1024) {
        @include vw(margin, 0 30px);
        width: calc(50% - 3.125vw);
      }

      @media screen and ($min1920) {
        margin: 0 30px;
        width: calc(50% - 60px);
      }
    }
  }

