    display: none !important;
  }
}
.none {
  display: none;
}

.ov-hidden {
  overflow: clip;
}

.skp-sc .ov-hidden {
  overflow: clip;
}

.ov-visible {
  overflow: visible;
}

.ov-o-hidden {
  opacity: 0;
  visibility: hidden;
}

.ov-o-visible {
  opacity: 1;
  visibility: visible;
}

input,
textarea {
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0;
}

select {
  border-radius: 0;
}

button,
input,
select,
textarea {
  font: inherit;
  margin: 0;
}

