[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

blockquote {
  padding: 0;
  margin: 0;
}

button {
  cursor: pointer;
  border: 0;
  background: transparent;
  padding: 0;
}

.pe-none {
  pointer-events: none;
}

.center {
  text-align: center;
}

.upper {
  text-transform: uppercase;
}

.lower {
  text-transform: lowercase;
}

.case {
  text-transform: normal;
}

.nocase {
  text-transform: none;
}

.italic {
  font-style: italic;
}

.hyphenate {
  overflow-wrap: break-word;
  word-wrap: break-word;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  -moz-hyphens: auto;
  hyphens: auto;
}

.prel {
  position: relative;
}

.pabs {
  position: absolute;
}

.pfix {
  position: fixed;
}

.valign {
  position: relative;
  display: table;
}
.valign.fit {
  width: 100%;
  height: 100%;
}
.valign .middle {
  display: table-cell;
  vertical-align: middle;
}

.middle {
  vertical-align: middle;
}

.bg-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 105%;
  height: 105%;
  backface-visibility: hidden;
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
}

.sf-dump {
  position: relative !important;
  z-index: 999999 !important;
}

